body {
  background: rgb(12, 0, 62);
  background: linear-gradient(
    90deg,
    rgba(12, 0, 62, 1) 0%,
    rgba(32, 22, 148, 1) 100%
  );
}

.container {
  margin: auto;
  margin-top: 10vh;
}

#inner-container {
  color: white;
}

.recommendation-container {
  margin-top: 10px;
  border-radius: 10px;
  background-color: rgba(110, 126, 129, 0.205);
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.2);
}

.movie-list-container {
  margin-top: 10px;
  border-radius: 20px;
}

.poster {
  max-width: 100%;
  height: auto;
}

.poster-container {
  padding: 10px;
}

.logos {
  width: 100%;
  max-width: 90px;
  margin: 0 auto;
  margin-top: 10px;
  margin-bottom: 10px;
  display: block;
}

#heading {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-size: 2rem;
  margin-bottom: 10pxs;
  text-align: center;
  color: #00b8db;
}

#movie-title {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-size: 1.6rem;
  color: #00b8db;
  margin-top: 0.3rem;
}

.meta-data p {
  margin-bottom: 0;
}

#meta {
  font-family: "Oswald", sans-serif;
  font-size: 1.3rem;
  color: #00b8db;
  text-transform: uppercase;
}

#meta a {
  font-family: "Oswald", sans-serif;
  font-size: 1.3rem;
  color: #00b8db;
  text-transform: uppercase;
}

#text-input {
  border-style: none;
  border-bottom: solid 2px white;
  font-size: 1.6rem;
  background: transparent;
  outline: none;
  width: 100%;
}

input,
select,
textarea {
  color: #ffffff;
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

::placeholder {
  color: rgb(211, 211, 211, 0.3);
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
}

#description {
  padding: none;
  margin-left: 8%;
  margin-right: 8%;
}

.count-btn {
  font-family: "Oswald", sans-serif;
  font-size: 1.4rem;
  color: white;
  background: transparent;
  border-style: none;
  margin: 10px;
}

.count-btn:focus,
.count-btn:hover {
  transform: scale(1.2);
  outline: none;
}

#count-output {
  color: rgba(32, 22, 148, 1);
}

.submit-btn {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;
  font-size: 1.4rem;
  color: white;
  background: #00b8db;
  border-style: none;
  border-radius: 10px;
  box-shadow: 0 0 20px 10px rgba(0, 0, 0, 0.2);
}

.submit-btn:focus,
.submit-btn:hover {
  transform: scale(1.01);
  outline: none;
}

.footer-container {
  font-size: 1rem;
  margin-bottom: 10px;
}

.footer-container p {
  margin-bottom: 0;
}

.checkbox-label input {
  opacity: 0;
  position: absolute;
}

.checkbox-label {
  margin-top: 0.3rem;
}

.checkbox-text {
  font-size: 0.9rem;
}

.checkbox-text:hover {
  cursor: pointer;
  font-size: 1rem;
}
